import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { SyncPublisherStratey } from '../constants';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { PublisherProcessingOptions, USyncActionView } from '../../api';

@customElement(SyncPublisherStratey.elements.result)
export class SyncPublishResultStepElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	connectedCallback() {
		super.connectedCallback();

		const changes = (this.results?.results['changes'] as number) ?? 0;

		this.dispatchEvent(
			new CustomEvent<boolean>('set-valid', {
				bubbles: true,
				composed: true,
				detail: changes != 0,
			}),
		);
	}

	render() {
		const actions = this.results?.results['actions'] as USyncActionView[];

		const publisherOptions = this.options as PublisherProcessingOptions;

		return html`
			<usync-publisher-report-view
				.server=${publisherOptions.server ?? ''}
				.isReport=${false}
				.results=${actions}></usync-publisher-report-view>
		`;
	}
}

export default SyncPublishResultStepElement;
